cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.node.on("touchstart",this.onTouched,this);
    },

    start () {

    },
    onTouched(event){
        var areaSelectUI = this.node.parent;
        var background = areaSelectUI.getChildByName("background");
        var xOffset = (this.node.getContentSize().width + background.getContentSize().width) / 2;
        var moveBackAction = cc.moveTo(0.2,cc.v2(-xOffset,0));
        var action = cc.sequence(moveBackAction,cc.removeSelf());
        areaSelectUI.runAction(action);
    } 

    // update (dt) {},
});
